.footer {
  max-width: 1600px;
  margin: 0 auto;

  background-color: $footer-bg;

  &__author {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    font-weight: 400;
    font-size: 0.875rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $footer-author;

    &--bolder {
      display: flex;

      font-weight: 600;
    }
  }
}

.footer-container {
  display: flex;
  justify-content: space-between;
  max-width: 1240px;
  margin: 0 auto;
  padding-top: 27px;
  padding-right: 30px;
  padding-bottom: 24px;
  padding-left: 30px;

  @media (max-width: 785px) {
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }
}

.copyright {
  font-weight: 400;
  font-size: 0.9375rem;
  font-family: "Montserrat", "Arial", sans-serif;
  text-align: center;
  color: $copyright;
}
