.testimonials {
  max-width: 1600px;
  margin: 0 auto;

  background: url("../img/testimonials-bg.jpg") no-repeat top;
  background-size: cover;

  &__quote {
    font-weight: 300;
    font-size: 1.4375rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $testimonials-quote;
    letter-spacing: 0.3px;
  }

  &__author-about {
    margin-top: 27px;

    font-weight: 400;
    font-size: 1.4375rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $testimonials-author-about;
    letter-spacing: 0.9px;
  }

  &__author-name {
    font-weight: 300;
    font-size: 1.4375rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $testimonials-author-name;
    font-style: normal;
    letter-spacing: 0.2px;
    word-spacing: 5px;
  }

  &__slider {
    margin-top: 81px;
    margin-bottom: 4px;
  }
}

.testimonials-container {
  max-width: 1240px;
  margin: 0 auto;
  padding-top: 129px;
  padding-right: 30px;
  padding-bottom: 37px;
  padding-left: 30px;

  text-align: center;
}

.testimonial-first {
  display: block;
  min-height: 139px;
}

.testimonial-second {
  display: none;
  min-height: 139px;
}

.testimonial-third {
  display: none;
  min-height: 139px;
}

.slider {
  display: flex;
  justify-content: center;

  &__dot {
    width: 10px;
    height: 10px;

    background-color: $slider-dot;
    border-radius: 100%;
    cursor: pointer;

    transition: background-color 0.2s;

    &:hover {
      background-color: $slider-dot-hover;

      transition: background-color 0.2s;
    }
  }

  &__dot-first {
    background-color: $slider-dot-hover;
  }

  &__dot-second {
    margin: 0 10px;
  }
}
