.stories {
  max-width: 1600px;
  margin: 0 auto;

  letter-spacing: -0.7px;

  background-color: $stories-bg;

  &__header {
    font-weight: 600;
    font-size: 2.25rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $stories-header;

    @media (max-width: 1216px) {
      text-align: center;
    }
  }

  &__divider {
    margin-top: 48px;
  }

  &__story {
    margin-top: 60px;
  }
}

.stories-container {
  max-width: 1240px;
  margin: 0 auto;
  padding-top: 96px;
  padding-right: 30px;
  padding-bottom: 116px;
  padding-left: 30px;
}

.divider {
  width: 66px;
  height: 2px;

  background-color: $stories-divider;

  @media (max-width: 1216px) {
    margin-right: auto;
    margin-left: auto;
  }
}

.stories-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @media (max-width: 1216px) {
    justify-content: space-around;
  }
}

.story {
  width: 100%;
  max-width: 380px;

  &__img {
    width: 100%;
    max-width: 380px;
    height: auto;
  }

  &__text {
    max-width: 370px;
    margin-top: 19px;

    font-weight: 300;
    font-size: 1.25rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $stories-text;
    letter-spacing: 0.3px;
  }

  &__continue {
    display: block;

    font-weight: 400;
    font-size: 1rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $stories-continue;
    letter-spacing: 0.1px;
  }

  &__link {
    display: block;
  }

  &__continue-link {
    display: inline-block;
    margin-top: 19px;
  }
}
