.btn {
  display: inline-block;

  border-radius: 3px;

  &--red {
    color: #ecf0f1;

    background-color: #f7564b;

    transition: background-color 0.3s;

    &:hover {
      background-color: #db4c42;

      transition: background-color 0.3s;
    }
  }

  &--border-red {
    border-bottom: 2px solid $border-red;
  }
}

.request-btn {
  padding-top: 15px;
  padding-right: 22px;
  padding-bottom: 15px;
  padding-left: 22px;

  font-weight: 400;
  font-size: 1rem;
  font-family: "Montserrat", "Arial", sans-serif;
  text-align: center;
}

.submit-btn {
  padding: 15.5px 60px;

  font-weight: 400;
  font-size: 1rem;
  font-family: "Montserrat", "Arial", sans-serif;
}
