.intro {
  max-width: 1600px;
  margin: 0 auto;

  background: url("../img/intro-bg.jpg") no-repeat top;
  background-size: cover;
}

.header {
  display: flex;
  justify-content: space-between;
  max-width: 1240px;
  margin: 0 auto;
  padding-top: 130px;
  padding-right: 30px;
  padding-left: 30px;

  @media (max-width: 1052px) {
    flex-direction: column;
  }
}

.logo {
  display: inline-flex;
  width: 177px;
  height: 20px;

  background: url("../img/logo.png") no-repeat top;
}

.nav {
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 700px;

  @media (max-width: 1052px) {
    margin-top: 20px;
  }

  @media (max-width: 825px) {
    flex-direction: column;
    align-items: flex-start;
    width: auto;
  }

  &__item {
    font-weight: 400;
    font-size: 1.2rem;
    line-height: 14px;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $nav-item;

    @media (max-width: 825px) {
      margin-top: 10px;

      font-size: 1.4rem;
    }
  }
}

.banner {
  max-width: 1240px;
  margin: 0 auto;
  padding-top: 184px;
  padding-right: 30px;
  padding-bottom: 147px;
  padding-left: 30px;

  &__header {
    max-width: 835px;

    font-weight: 700;
    font-size: 3.75rem;
    line-height: 61px;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $banner-header;
    letter-spacing: -1.6px;

    @media (max-width: 535px) {
      font-size: 2.8rem;
    }
  }

  &__text {
    max-width: 546px;
    margin-top: 21px;

    font-weight: 300;
    font-size: 1.5rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $banner-text;
    letter-spacing: -0.1px;
  }

  &__conclusion {
    margin-top: 21px;

    font-weight: 300;
    font-size: 1.5rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $banner-conclusion;
  }

  &__conclusion--bolder {
    font-weight: 700;
    letter-spacing: -1px;
  }

  &__request {
    margin-top: 26px;
  }

  &__request-btn {
    margin-left: 17px;

    @media (max-width: 555px) {
      display: block;
      width: 100%;
      margin-left: 0;
    }
  }
}

.request {
  max-width: 480px;

  &__email-input {
    width: 279px;
    padding: 15px;

    font-weight: 300;
    font-size: 0.875rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $banner-email-input;

    background-color: $banner-email-input-bg;
    border-bottom: 2px solid $border-grey;
    border-radius: 3px;
    outline: none;

    @media (max-width: 555px) {
      display: block;
      width: 100%;

      text-align: center;
    }
  }
}
