.contact {
  max-width: 1600px;
  margin: 0 auto;

  background-color: $contact-bg;

  &__header {
    font-weight: 700;
    font-size: 1.875rem;
    font-family: "Montserrat", "Arial", sans-serif;
    text-align: center;
    color: $contact-header;
    word-spacing: -4px;
  }
}

.contact-container {
  max-width: 1240px;
  margin: 0 auto;
  padding-top: 73px;
  padding-right: 30px;
  padding-bottom: 83px;
  padding-left: 30px;
}

.contact-inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 68px;

  @media (max-width: 815px) {
    justify-content: space-around;

    text-align: center;
  }
}

.contact-form {
  max-width: 381px;

  &__form {
    margin-top: 23px;
  }

  &__header {
    font-weight: 400;
    font-size: 1.5rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $contact-form-header;
  }

  &__name-input {
    width: 47.3%;
    padding-top: 17px;
    padding-right: 16px;
    padding-bottom: 17px;
    padding-left: 16px;

    font-weight: 300;
    font-size: 0.875rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $contact-form-input;

    background-color: $contact-form-input-bg;
    border-radius: 3px;
    outline: none;

    @media (max-width: 443px) {
      width: 100%;
    }
  }

  &__email-input {
    width: 47.8%;
    margin-left: 14px;
    padding-top: 17px;
    padding-right: 18px;
    padding-bottom: 17px;
    padding-left: 18px;

    font-weight: 300;
    font-size: 0.875rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $contact-form-input;

    background-color: $contact-form-input-bg;
    border-radius: 3px;
    outline: none;

    @media (max-width: 443px) {
      width: 100%;
      margin-top: 16px;
      margin-left: 0;
    }
  }

  &__message-input {
    display: block;
    width: 100%;
    height: 130px;
    margin-top: 16px;
    padding: 17px 17px;

    font-weight: 300;
    font-size: 0.875rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $contact-form-input;

    background-color: $contact-form-input-bg;
    border-radius: 3px;
    outline: none;

    resize: none;
  }

  &__submit-btn {
    margin-top: 19px;

    @media (max-width: 815px) {
      width: 100%;

      text-align: center;
    }
  }
}

.address {
  max-width: 358px;
  padding-top: 3px;

  line-height: 23px;
  text-align: right;
  font-style: normal;

  @media (max-width: 815px) {
    margin-top: 80px;

    text-align: center;
  }

  &__header {
    margin-bottom: 23px;

    font-weight: 600;
    font-size: 1.5rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $address-header;
  }

  &__text {
    margin-bottom: 27px;

    font-weight: 600;
    font-size: 1.25rem;
    line-height: 1;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $address-text;
    letter-spacing: -0.4px;
  }
}
